<template>

  <el-card class="login-box" :body-style="{ padding: '16px' }">
    <div class="me-article-header">

      <a @click="view(id)" class="me-article-title">{{title}}</a>
      <el-button v-if="weight > 0" class="me-article-icon" type="text">置顶</el-button>
<!--
      <span class="me-pull-right me-article-count">
	    	<i class="el-icon-delete" style="color: red" @click=""></i>&nbsp;
	    </span>
-->

      <span class="me-pull-right me-article-count">
        <i class="el-icon-document" style="padding-top: 2px"></i>&nbsp;{{commentCounts}}
	    </span>

      <span class="me-pull-right me-article-count">
        <img src="static/tag/zan.png" style="width: 15px;height: 15px;"></img> 0
      </span>
      <span class="me-pull-right me-article-count">
	    	<i class="el-icon-view" style="padding-top: 2px"></i>&nbsp;{{viewCounts}}
	    </span>
<!--      <el-button v-if="this.props.id === parseInt(this.$store.state.id)" size="mini" round>删除</el-button>-->
    </div>

    <div class="me-artile-description">
      {{summary}}
    </div>

    <div class="me-article-footer">
	  	<span class="me-article-author">
	    	<i class="el-icon-user"></i>&nbsp;{{author}}
	    </span>

      <el-tag v-for="t in tags" :key="t.tagName" size="mini" type="success">{{t.tagName}}</el-tag>
      <span class="me-pull-right me-article-count">
	    	<i class="el-icon-time"></i>&nbsp;{{createDate | format}}
	    </span>
<!--     地址 -->
      <span class="me-pull-right me-article-count">
        <i class="el-icon-location">{{ authorIp }}</i>
      </span>

    </div>
  </el-card>
</template>

<script>

  import { formatTime } from "../../utils/time";
  import request from '@/request/token';
  import {getUserId} from '@/api/login'
  import { deleteById } from '@/api/article'
  /*import {getUserIP}  from '@/api/getUserIP'*/
  export default {
    cname:'',
    name: 'ArticleItem',
    //id: '',
    props: {
      id: String,
      weight: Number,
      title: String,
      commentCounts: Number,
      viewCounts: Number,
      summary: String,
      author: String,
      tags: Array,
      createDate: String,
      authorIp: String
    },
    created() {
      /*this.getUserInfo()*/
      /*getUserIP((ip) => {
        this.ip = ip;
        console.info(this.ip)
      })*/
      this.cname = localStorage.getItem('cityname');
      /*console.log(localStorage.getItem('Ip'))
      console.log(localStorage.getItem('cityname'))*/
    },
    data() {
      return {
      };
    },
    methods: {
      view(id) {
        console.info(this.$route.params.id)
        this.$router.push({path: `/view/${id}`})
      },
      deleteById() {
        deleteById(this.$route.params.id).then(res => {
          if(res.data){
            this.$message({type: 'success', message: '删除成功!', showClose: true})
            this.$router.push({path: '/'})
          }else {
            this.$message({type: 'error', message: '删除失败!', showClose: true})
          }
        }).catch(err => {
          this.$message({type: 'error', message: '删除失败!', showClose: true})
        })
      }

    }
  }
</script>

<style scoped>

  .me-article-header {
    /*padding: 10px 18px;*/
    padding-bottom: 10px;
  }

  .me-article-title {
    font-weight: 600;
  }

  .me-article-icon {
    padding: 3px 8px;
  }

  .me-article-count {
    color: #a6a6a6;
    padding-left: 14px;
    font-size: 13px;
  }

  .me-pull-right {
    float: right;
  }

  .me-artile-description {
    font-size: 13px;
    line-height: 24px;
    margin-bottom: 10px;
  }

  .me-article-author {
    color: #a6a6a6;
    padding-right: 18px;
    font-size: 13px;
  }

  .el-tag {
    margin-left: 6px;
  }
  .login-box{

    background-color: rgba(104, 107, 199, 0.5);
  }
</style>



